import React from "react";
import Projects from "../assets/Projects.json";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import "../styles/Card.scss";
import { Link } from "react-router-dom";

const card = () => {
  return (
    <div className="container">
      {Projects.map(details => {
        return (
          <div className="card" key={details.id}>
            <h5>{details.name}</h5>
            <div>
              <FontAwesomeIcon icon={details.icons} className="icons" />
            </div>
            <p className="tools">{details.tools.join(", ")}</p>
            <ul>
              <li>
                <Link
                  to={details.deploy_link}
                  target="_blank"
                  className="deploy_link"
                >
                  {details.deploy_link ? "View Project" : null}
                </Link>
              </li>
              <li>
                <Link
                  to={details.code_link}
                  target="_blank"
                  className="code_link"
                >
                  {details.code_link ? "View Code" : null}
                </Link>
              </li>
            </ul>
            <p className="description">{details.description}</p>
          </div>
        );
      })}
    </div>
  );
};

export default card;
